<template>
  <div class='container-subjects'>
    <el-card>
      <!-- 筛选 -->
      <el-form inline  size="small">
      <el-form-item label="学科名称" style="padding-left: 12px;">
        <el-input v-model="obj.subjectName"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button @click="onClear">清除</el-button>
        <el-button type="primary" @click="onSearch">搜索</el-button>
      </el-form-item>
      <el-button @click="handleClose('add')" size="small" style="float: right;" type="success" icon="el-icon-edit">新增学科</el-button>
    </el-form>
      <!-- 总计 -->
      <!-- Alert 警告消息提示 -->
      <el-alert
        :title="`数据一共 ${total} 条`"
        type="info"
        show-icon
        :closable="false"
        style="margin-bottom: 15px;">
      </el-alert>
      <!-- 表格 -->
      <template>
        <el-table
          :data="subjects"
          :header-cell-style="{background:'#fafafa'}"
          style="width: 100%"
          >
          <el-table-column width="48" label="序号" type="index"></el-table-column>
          <el-table-column width="169" label="学科名称" prop="subjectName"></el-table-column>
          <el-table-column min-width="165" label="创建者" prop="username"></el-table-column>
          <el-table-column min-width="160" label="创建日期">
            <template slot-scope="scope">
              <!-- parseTimeByString过滤器在filters文件夹中 -->
              {{ scope.row.addDate|parseTimeByString }}
            </template>
          </el-table-column>
          <el-table-column min-width="165" label="前台是否显示">
            <template slot-scope="scope">
              {{ scope.row.isFrontDisplay === 1 ? '是':'否' }}
            </template>
          </el-table-column>
          <el-table-column min-width="165" label="二级目录" prop="twoLevelDirectory"></el-table-column>
          <el-table-column min-width="165" label="标签" prop="tags"></el-table-column>
          <el-table-column min-width="165" label="题目数量" prop="totals"></el-table-column>
          <el-table-column min-width="240" label="操作">
            <template  slot-scope="scope">
              <el-link @click="toDiretory(scope.row)" :underline="false" type="primary">学科分类</el-link>
              <el-link @click="toTags(scope.row)" :underline="false" type="primary">学科标签</el-link>
              <el-link @click="handleClose('edit',scope.row)" :underline="false" type="primary">修改</el-link>
              <el-link @click="delSubject(scope.row.id)" :underline="false" type="primary">删除</el-link>
            </template>
          </el-table-column>
        </el-table>
      </template>
      <!-- 分页 changePage改变页码 changeSize改变尺码 -->
      <el-pagination
        style="float: right;margin: 15px 0 15px;"
        background
        layout="prev,pager,next,sizes,jumper"
        :total="total"
        :page-size="obj.pagesize"
        :current-page="obj.page"
        @current-change="changePage"
        @size-change="changeSize"
        :page-sizes="[5,10,20,50]"
        >
      </el-pagination>
    </el-card>
    <!-- 父传子 -->
    <getDailog ref="addDailog" :list="list" @addon="getSubjects"></getDailog>
  </div>
</template>

<script>
import getDailog from '@/module-hmmm/components/subjects-add.vue'
// 学科列表api
import { list as subjectsList, remove as removeSubjects } from '@/api/hmmm/subjects.js'
// import getDialog from '@/module-hmmm/components/subjects-add.vue'

export default {
  name: 'subjects-page',
  components: {
    getDailog
  },
  data () {
    return {
      obj: {
        // 学科名称
        subjectName: '',
        // 当前页数
        page: 1,
        // 页尺寸,当前页条数
        pagesize: 10
      },
      // 学科列表
      subjects: [],
      // 总条数
      total: 0,
      // 点击添加或修改模态框
      list: {
        type: 'add',
        row: {}
      }
    }
  },
  created () {
    this.getSubjects()
  },
  methods: {
    // 修改
    handleClose (type, row) {
      this.list.type = type
      this.list.row = row
      // console.log(this.list.type)
      this.$refs.addDailog.open()
    },
    // 清除功能
    onClear () {
      this.obj.subjectName = ''
    },
    // 搜索功能
    onSearch () {
      // 为第一页
      this.obj.page = 1
      this.getSubjects()
    },
    async getSubjects () {
      const { data } = await subjectsList(this.obj)
      // console.log(data)
      this.subjects = data.items
      // console.log(this.subjects)
      this.total = data.counts
    },
    // 切换分页
    changePage (newPage) {
      this.obj.page = newPage
      this.getSubjects()
    },
    // 切换尺码
    changeSize (newSize) {
      this.obj.page = 1
      this.obj.pagesize = newSize
      this.getSubjects()
    },
    // scope.row获取的是整行的值可以是个对象 scope.$index获取是从0开始排序的索引值
    // 去目录
    toDiretory (row) {
      // console.log(row, '数据查看')
      const { id, subjectName: name } = row
      // 编程式导航
      this.$router.push({ path: 'directorys', query: { id, name } })
    },
    // 去标签
    toTags (row) {
      const { id, subjectName: name } = row
      this.$router.push({ path: 'tags', query: { id, name } })
    },
    // 删除
    delSubject (id) {
      this.$confirm('此操作将永久删除该学科, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        await removeSubjects({ id })
        this.$message({
          type: 'success',
          message: '删除成功!'
        })
        this.getSubjects()
      }).catch((e) => {})
    }
  }
}
</script>

<style scoped lang="scss">
.container-subjects{
  // padding: 10px;

.el-link{
  margin-right: 10px;
}
::v-deep .el-table{
  width: 100% !important;
}
}
</style>
